import Vue from 'vue';
import { Component } from 'vue-property-decorator';
var Paginate = require('vuejs-paginate')
Vue.component('paginate', Paginate)

interface Country {
    Countryname: string;
    Countryid: number;
    Description: string;   
    Countrycode: string;
    Createddate: Date;
    Updateddate: Date;
}

@Component({
    components: {
      
        MenuComponent: require('../navmenu/navmenu.vue.html')
    }
})
export default class AppComponent extends Vue {
 
    Countries: Country[];
    deletedCountry: Country;
    showModal: string   
    Modalshow: string
    Countryname: string
    Countrycode: string
    Description: string
    details: Country[]
    totalpages: string
  
    data() {
        return {
            Countries: [],  
            details: [],
            showModal: 'none' ,
            Modalshow: 'none', 
            Countryname: '',
            Countrycode: '',
            Description: '',          
           totalpages:''
           
        };
    }
    mounted() {

        //fetch('/Countries/Index')
        //    .then(response => response.json() as Promise<Country[]>)
        //    .then(data => {
        //        debugger;
        //        this.Countries = data;
        //    }); 


        fetch('/Countries/Index')
            .then(response => response.json())
            .then(data => {
                this.ParseJsonResponseData(data);
             
            }); 

       
            
    }
    ParseJsonResponseData(data: any) {     
                var myObject: Country[];              
                // values are assigned to them, and...
                myObject = <Country[]>data.obj;   // using <>
                this.Countries = [];
                // values are assigned to t
                myObject.forEach(item => { this.Countries.push(item); });
                if (myObject.length > 0) {
                    this.totalpages = JSON.stringify(data.totalpages);
                }

    }
    addcountry() {
        //this.Countryname = '';
        //this.Countrycode = '';
        //this.Description = '';
        this.Modalshow = 'block';
    }
    SaveOrEditCountry() {
        const Country = {
            Countryname: this.Countryname,
            Countrycode: this.Countrycode,
            description: this.Description
        };
        fetch('/Countries/AddorEditCountry', {
            method: 'post',
            body: JSON.stringify(Country),
            headers: new Headers({
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            })
        })
            .then(response => response.json() as Promise<Country>)
            .then((newItem) => {
                this.Countries.push(newItem);
                this.showModal = 'none';
                this.Modalshow = 'none';   
            });
        this.showModal = 'none';
        this.Modalshow = 'none';   
    }
    deleteCountry(item: Country) {
        this.details.push(item);
       
        this.showModal = 'block';
        this.deletedCountry = item;
    }
    closeModal() {
        this.showModal = 'none';  
        this.Modalshow = 'none'; 
        
    }
    submitAndClose() {
        let index = this.Countries.indexOf(this.details[0]);
        this.Countries.splice(index, 1);
            fetch('/Countries/DeleteCountry', {
                method: 'post',
                body: JSON.stringify(this.details[0]),
                headers: new Headers({
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                })
            })
                .then(response => response.json() as Promise<Country>)
                .then((newItem) => {
                   
                });
        this.showModal = 'none';
    }
    editCountry(item: Country) {
      
        this.Countryname = item.Countryname;
        this.Countrycode = item.Countrycode;
        this.Description = item.Description;
        this.Modalshow = 'block';
    }
   
    clickCallback(pageNum: number) {
      
        fetch('/Countries/Index' + "?page=" + pageNum)
            .then(response => response.json())
            .then(data => {
                this.ParseJsonResponseData(data);
               
            }); 

    }
}
  


