import './css/site.css';
import 'bootstrap';
import Vue from 'vue';
import VueRouter from 'vue-router';
Vue.use(VueRouter);

const routes = [
    { path: '/Countries', component: require('./components/Countries/Countries.vue.html') },
    { path: '/App_Configs', component: require('./components/App_Configs/AppConfigs.vue.html') },
    { path: '/UserDefaults', component: require('./components/UserDefaults/UserDefaults.vue.html') }
];

new Vue({
    el: '#app-root',
    router: new VueRouter({ mode: 'history', routes: routes }),
    render: h => h(require('./components/app/app.vue.html'))
});



